:root {
  --accent: #3c6e71;
  --dark3: #212529;
  --dark2: #343a40;
  --dark1: #495057;
  --med3: #6c757d;
  --med2: #adb5bd;
  --med1: #ced4da;
  --light3: #dee2e6;
  --light2: #e9ecef;
  --light1: #f8f9fa;
}

* {
  font-family: "Raleway", sans-serif;
}

body {
  margin: 0;
  background-image: linear-gradient(#212529 0%, #ced4da 100%);
  background-image: linear-gradient(var(--dark3) 0%, var(--med1) 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --accent: #3c6e71;
  --dark3: #212529;
  --dark2: #343a40;
  --dark1: #495057;
  --med3: #6c757d;
  --med2: #adb5bd;
  --med1: #ced4da;
  --light3: #dee2e6;
  --light2: #e9ecef;
  --light1: #f8f9fa;
}

.nameHeader {
  width: 100vw;
  padding: 10px;
  text-shadow: 0 0 10px #3c6e71, 0 0 15px #3c6e71, 0 0 30px;
  text-shadow: 0 0 10px var(--accent), 0 0 15px var(--accent), 0 0 30px;
}

.nameText {
  text-align: center;
  color: #f8f9fa;
  color: var(--light1);
  font-family: "Petit Formal Script";
  font-size: 6vw;
}

.navBar {
  width: 100%;
  display: flex;
  justify-content: space-around;
  padding: 10px;
}

.navBtn {
  border-radius: 50%;
  border: solid 2px #3c6e71;
  border: solid 2px var(--accent);
  box-shadow: 0 0 10px #3c6e71;
  box-shadow: 0 0 10px var(--accent);
  width: 120%;
  background: #212529;
  background: var(--dark3);
  color: #f8f9fa;
  color: var(--light1);
  font-size: 3vw;
}

.navBtn:hover {
  color: #3c6e71;
  color: var(--accent);
  box-shadow: 0 0 10px #f8f9fa;
  box-shadow: 0 0 10px var(--light1);
}

.navLink {
  text-decoration: none;
  color: #f8f9fa;
  color: var(--light1);
}

/* When viewport width surpasses typical smartphone size */
@media only screen and (min-width: 601px) {
  header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .navBtn {
    font-size: 2vw;
  }
}

:root {
  --accent: #3c6e71;
  --dark3: #212529;
  --dark2: #343a40;
  --dark1: #495057;
  --med3: #6c757d;
  --med2: #adb5bd;
  --med1: #ced4da;
  --light3: #dee2e6;
  --light2: #e9ecef;
  --light1: #f8f9fa;
}

.aboutContainer {
  color: #f8f9fa;
  color: var(--light1);
  margin: 8%;
}
.welcome {
  display: flex;
  justify-content: center;
  align-items: center;
}

.headshot {
  width: auto;
  height: 8rem;
  border-radius: 50%;
  margin: 1rem;
}

.skillsContainer {
  color: #3c6e71;
  color: var(--accent);
  margin: 8%;
  margin-top: 20%;
}
.aboutContainer h3,
.skillsContainer h3 {
  font-size: 3.5rem;
}

.skillsContainer h3 {
  font-family: "Petit Formal Script";
  text-align: center;
}

.aboutContainer p,
.skillsContainer p {
  font-size: 1.5rem;
  margin-top: 3%;
}

.iconsDiv {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
}
.icon {
  color: #3c6e71;
  color: var(--accent);
  font-size: 3rem;
  margin: 3rem;
}

