.gallery {
  display: flex;
  align-items: center;
  justify-content: center;
  /* max-height: 60vh; */
  width: 90vw;
  margin-top: 12vh;
  margin: 2vw;
}

.galleryBtn {
  display: flex;
  align-items: center;
  justify-content: center;
  background: transparent;
  margin: 1rem;
  font-size: 2rem;
  border: transparent;
}

.galleryBtn i {
  color: #f8f9fa;
  color: var(--light1);
}

.galleryBtn i:hover,
.galleryBtn i:focus {
  color: #212529;
  color: var(--dark3);
  text-shadow: 0 0 10px #ced4da, 0 0 15px #ced4da, 0 0 30px;
  text-shadow: 0 0 10px var(--med1), 0 0 15px var(--med1), 0 0 30px;
}

.projectCard {
  background-color: #212529;
  background-color: var(--dark3);
  max-width: 70%;
  border-radius: 15px;
  margin: 1rem;
}

.imageDiv {
  width: auto;
  height: 60%;
}

.projectImg {
  border-radius: 15px 15px 0 0;
}

.details {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  width: 100%;
}

.title {
  font-family: "Petit Formal Script";
  color: #f8f9fa;
  color: var(--light1);
  font-size: 5vw;
}

.linkDiv {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.projectLinks {
  -webkit-text-decoration: "none";
          text-decoration: "none";
  color: #adb5bd;
  color: var(--med2);
  font-size: 2rem;
}
.projectLinks:hover {
  text-decoration: none;
  color: #3c6e71;
  color: var(--accent);
}

:root {
  --warning: #e14c80;
  --accent: #3c6e71;
  --dark3: #212529;
  --dark2: #343a40;
  --dark1: #495057;
  --med3: #6c757d;
  --med2: #adb5bd;
  --med1: #ced4da;
  --light3: #dee2e6;
  --light2: #e9ecef;
  --light1: #f8f9fa;
}

.contactFormDiv {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #f8f9fa;
  color: var(--light1);
  background-color: #495057;
  background-color: var(--dark1);
  border-radius: 5%;
  width: 70%;
  margin: 2rem;
}

.contactFormDiv h3 {
  margin: 1rem;
}

.contactFormDiv h6 a {
  color: #f8f9fa;
  color: var(--light1);
}
.contactFormDiv h6 a:hover {
  color: #212529;
  color: var(--dark3);
  text-decoration: none;
}

form {
  display: flex;
  flex-direction: column;
  justify-content: start;
  width: 100%;
  margin: 2rem;
  padding: 2rem;
}

.feedbackText {
  text-align: end;
  font-size: 0.5rem;
  margin: 0;
  color: #e14c80;
  color: var(--warning);
}

.custom-btn {
  align-self: center;
  background-color: #3c6e71;
  background-color: var(--accent);
  color: #212529;
  color: var(--dark3);
  margin: 1rem;
  padding: 10px;
  width: 25%;
}
.custom-btn:hover {
  background-color: #212529;
  background-color: var(--dark3);
  color: #f8f9fa;
  color: var(--light1);
  margin: 1rem;
  padding: 10px;
  width: 25%;
}

